"use client";
import { PaystackButton } from "react-paystack";
import { useRouter } from "next/navigation";

export default function PayWithPaystack({ product, email }) {
  const router = useRouter();
  // We use the key from your Vercel settings
  const publicKey = process.env.NEXT_PUBLIC_PAYSTACK_PUBLIC_KEY;

  if (!publicKey) {
    return (
        <div className="p-4 bg-red-900/20 border border-red-500 text-red-500 rounded text-center">
            <strong>System Error:</strong> Paystack Key not found in .env
        </div>
    );
  }

  return (
    <div className="w-full">
        <PaystackButton 
            email={email}
            amount={product.price * 100} // KES to Cents
            currency="KES"
            metadata={{ custom_fields: [{ display_name: "Product ID", variable_name: "product_id", value: product.id }] }}
            publicKey={publicKey}
            text={`Pay KES ${product.price}`}
            onSuccess={() => router.push("/dashboard?payment=success")}
            className="w-full py-4 bg-blue-600 text-white font-bold text-xl rounded-xl hover:bg-blue-700 transition shadow-lg"
        />
        <p className="text-xs text-center text-gray-500 mt-3">Secured by Paystack</p>
    </div>
  );
}
